import React, { Component } from 'react'
import {
  Upload,
  Form,
  Input,
  Icon,
  Button,
  Typography,
  Select,
  Modal
} from 'antd'
import { genOrder, getWord } from './api'
import './App.css'

const { Title } = Typography
const { TextArea } = Input
const { Option } = Select

const { NODE_ENV } = process.env
const actionUploadLink =
  NODE_ENV === 'development'
    ? 'http://localhost:3000/common/upload'
    : 'https://www.sevenrabbit.cn/common/upload'
class App extends Component {
  constructor() {
    super()
    this.state = {
      neckTagType: 0,
      neckTag: [],
      preview: [],
      disabled: false
    }
  }

  neckTagTypeChange = type => {
    this.setState({
      neckTagType: type
    })
  }
  handleSubmit = form => e => {
    if (e) {
      e.preventDefault()
    }
    form.validateFields(async (err, values) => {
      console.log('Received values of form: ', values)
      if (!err) {
        if (this.state.preview.length < 1) {
          return
        }
        const neckTag =
          values.neckTagType === 2
            ? this.state.neckTag.length > 0 && this.state.neckTag[0].id
            : 0
        this.handleDisabled()
        const pkg = await genOrder({
          ...values,
          preview: this.state.preview.map(v => v.id),
          neckTag,
          overWrite: form.overWrite
        })
        if (pkg.err) {
          if (pkg.error.statusCode === 409) {
            return Modal.confirm({
              mask: false,
              content: `单号重复了，是否要直接覆盖掉原来这个单号的单子，当前单号为[${
                pkg.error.transactionCode
              }]`,
              onOk: () => {
                this.handleSubmit({ ...this.props.form, overWrite: true })()
              },
              okText: '好的，覆盖这个原来这个单号的单子',
              cancelText: '不了不了我看看单号先'
            })
          }
          return
        }
        if (pkg) {
          getWord([
            { path: pkg.url1, filename: pkg.filename1 },
            { path: pkg.url2, filename: pkg.filename2 }
          ])
        }
        form.resetFields()
        this.setState({
          neckTagType: 0,
          neckTag: [],
          preview: []
        })
      }
    })
  }
  handleDisabled = () => {
    this.setState({
      disabled: true
    })
    setTimeout(
      () =>
        this.setState({
          disabled: false
        }),
      2000
    )
  }
  handlePreviewChange = info => {
    let { fileList } = info
    console.log(fileList)
    this.setState({
      preview: fileList.map(f => ({
        ...f.response,
        name: '效果图.jpeg',
        uid: f.uid,
        url: f.response && f.response.originUrl,
        status: f.status,
        response: f.response
      }))
    })
  }

  handleNeckTagChange = info => {
    let { fileList } = info
    this.setState({
      neckTag: fileList.map(f => ({
        ...f.response,
        name: '领标图.jpeg',
        uid: f.uid,
        url: f.response && f.response.originUrl,
        status: f.status
      }))
    })
  }

  handleNeckTagRemove = info => {
    const { neckTag: p } = this.state
    let target
    if (info.response) {
      target = info.response.idtarget
    } else {
      target = info.id
    }
    const neckTag = p.filter(d => d.id !== target)
    this.setState({ neckTag })
  }
  handlePreviewRemove = info => {
    const { preview: p } = this.state
    let target
    if (info.response) {
      target = info.response.idtarget
    } else {
      target = info.id
    }
    const preview = p.filter(d => d.id !== target)
    this.setState({ preview })
  }

  render() {
    const { form } = this.props
    const { getFieldDecorator } = form
    const { preview, neckTagType, neckTag, disabled } = this.state
    const uploadButton = text => (
      <div>
        <Icon type="plus" />
        <div className="ant-upload-text">{text}</div>
      </div>
    )

    const formItemLayout = {
      labelCol: { span: 3 },
      wrapperCol: { span: 21 }
    }

    return (
      <div className="background">
        <Title level={4}>
          终极蛇皮上帝视角之错误猎手四重过滤之新版本最强下单套路之word生成器
        </Title>
        <div style={{ margin: '12px 0' }} />
        <div className="form-card">
          <Form
            onSubmit={this.handleSubmit(form)}
            className="login-form"
            {...formItemLayout}
          >
            <Form.Item label="下单信息">
              {getFieldDecorator('message', {
                rules: [{ required: true, message: '必须填下单信息' }]
              })(
                <TextArea
                  placeholder="下单信息"
                  autosize={{ minRows: 4, maxRows: 12 }}
                />
              )}
            </Form.Item>
            <Form.Item label="效果图">
              <Upload
                action={actionUploadLink}
                listType="picture-card"
                multiple
                fileList={preview}
                onChange={this.handlePreviewChange}
                onRemove={this.handlePreviewRemove}
              >
                {preview.length >= 10 ? null : uploadButton('上传效果图')}
              </Upload>
            </Form.Item>
            <Form.Item label="领标相关">
              {getFieldDecorator('neckTagType', {
                initialValue: 0
              })(
                <Select initialValue={0} onChange={this.neckTagTypeChange}>
                  <Option value={0}>七兔领标</Option>
                  <Option value={1}>空白领标</Option>
                  <Option value={2}>客制领标</Option>
                </Select>
              )}
            </Form.Item>
            {neckTagType === 2 ? (
              <Form.Item label="领标图">
                <Upload
                  action={actionUploadLink}
                  listType="picture-card"
                  fileList={neckTag}
                  onChange={this.handleNeckTagChange}
                  onRemove={this.handleNeckTagRemove}
                >
                  {neckTag.length >= 1 ? null : uploadButton('上传领标图')}
                </Upload>
              </Form.Item>
            ) : (
              ''
            )}
            {/* <Form.Item label="急单吗">
              {getFieldDecorator('isHurry', {
                valuePropName: 'checked',
                initialValue: false
              })(<Switch />)}
            </Form.Item> */}
            <Form.Item wrapperCol={{ span: 24 }}>
              <Button
                type="primary"
                block
                icon="fire"
                style={{
                  backgroundColor: '#E85A71',
                  borderColor: '#E85A71'
                }}
                disabled={disabled}
                htmlType="submit"
                className="login-form-button"
              >
                超级瞄准已启动，点击发射
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    )
  }
}

export default Form.create({ name: 'normal_login' })(App)
